.event.list {
    a:not(.button) {
        color: var(--text); }
    ul {
        list-style: none;
        padding-left: 0; }
    .heading {
        border-bottom: var(--border-line);
        margin-top: 1em;
        margin-bottom: 1em;
        padding-bottom: .25rem;
        display: flex;
        align-items: baseline;
        @media screen and (min-width: 1023px) {
            margin-bottom: 2em; }
        .headsub {
            text-transform: capitalize;
            font-size: 1.5em; }
        h6 {
            display: none;
            @media screen and (min-width: 480px) {
                display: block;
                font-weight: 500;
                margin-left: auto; } } }
    .row,
    .main,
    .taxo,
    .years,
    .month,
    .lister,
    .yearlist,
    .feature {
        display: grid;
        grid-gap: 1rem;
        @media screen and (min-width: 768px) {
            grid-gap: 2rem; }
        .row {
            grid-template-columns: 3rem auto;
            margin: 1em 0;
            padding: 1em 0; } }
    .item {
        border-bottom: var(--border-line); }
    .box {
        border-right: 1px solid orange;
        width: 3rem;
        height: 3rem;
        .date {
            font-weight: 600;
            font-size: 1.5rem; }
        .month {
            font-size: .9rem;
            margin-top: 0em; } }
    .leads {
        font-size: .9rem; }
    .main {
        grid-template-columns: auto;
        margin-top: 1em;
        .listing {
            display: grid; }
        @media screen and (min-width: 480px) {
            margin-top: 2em;
            .listing {
                grid-template-columns: auto; } }
        @media screen and (min-width: 768px) {
            .listing {
                grid-gap: 0 2em;
                grid-template-columns: auto auto; } }
        @media screen and (min-width: 1023px) {
            margin-top: 3em;
            grid-template-columns: 21% auto 21%;
            .listing {
                grid-template-columns: auto; } } }
    .taxo,
    .years,
    .month, {
        grid-template-columns: auto;
        margin-top: 3em;
        grid-gap: 3rem;
        @media screen and (min-width: 1024px) {
            grid-template-columns: auto 23%; }
        .listevents {
            .row {
                border-bottom: 0;
                margin: 0;
                padding: 1em 0; }
            .box .month {
                margin-top: 0; } } }
    .yearlist {
        grid-gap: 0rem;
        border-bottom: var(--border-line);
        @media screen and (min-width: 768px) {
            padding: 2rem 0;
            grid-template-columns: auto; }
        @media screen and (min-width: 1024px) {
            grid-template-columns: 13rem auto; }
        .evitems {
            display: grid;
            grid-template-columns: auto;
            grid-gap: 1rem;
            @media screen and (min-width: 768px) {
                grid-template-columns: auto auto;
                grid-gap: 2rem; }
            @media screen and (min-width: 1024px) {
                grid-template-columns: auto; } }
        .heading {
            @media screen and (min-width: 1024px) {
                display: block; }
            border-bottom: 0; }
        .item {
            border-bottom: 0; } }
    .monthlist {
        .feature {
            grid-template-columns: auto;
            @media screen and (min-width: 768px) {
                grid-template-columns: auto auto; }
            .thumb,
            .thumb img,
            .slider-con {
                height: 17rem; }
            .item {
                border-bottom: 0; }
            .box {
                border-right: 0px; } }
        .lister {
            grid-template-columns: auto;
            border-top: var(--border-line);
            margin-top: 2rem;
            padding-top: 2rem;
            @media screen and (min-width: 768px) {
                grid-template-columns: 23% auto; } } }
    .termlist {
        .monthlist {
            border-bottom: var(--border-line);
            padding-bottom: 2rem;
            margin-bottom: 3rem; }
        h4 {
            margin-bottom: 1rem; }
        .item {
            border-bottom: 0;
            .row {
                padding: 0;
                @media screen and (min-width: 1024px) {
                    padding: 1em 0; } } } }
    .listevents {
        margin-bottom: 1.4em;
        .title {
            font-size: 1.05rem; }
        .leads {
            font-size: .85re; } }
    .modal {
        position: fixed;
        display: grid;
        top: 0;
        left: 0;
        z-index: 10000000;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, .4);
        .contain {
            background: var(--bg);
            border-radius: 10px;
            overflow: hidden; }
        .title,
        .close-modal {
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center; }
        h2 {
            margin: 0 !important;
            font-size: 1rem !important; }
        .context {
            overflow-y: scroll !important;
            border-radius: 0 !important;
            height: calc( 100% - 100px ); }
        .close-modal {
            font-size: .9rem ; } }
    .list-action {
        display: flex;
        justify-content: center;
        @media screen and (min-width: 1024px) {
            display: none; }
        .contain {
            display: flex;
            position: fixed;
            bottom: 1.5rem;
            left: 1rem;
            background: var(--text);
            color: var(--bg);
            align-items: center;
            border: var(--border-line);
            border-radius: 10px;
            font-size: .85rem;
            .icon {
                width: 1.7rem;
                filter: invert(1); }
            .modal-action {
                cursor: pointer;
                padding: .95rem 1.5rem; } } } }
